/**************************************SASS*******************************************/
html {
  box-sizing: border-box;
  font-size: 62.5%; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  font-size: 1.6rem;
  font-family: "Montserrat", sans-serif; }

img {
  max-width: 100%; }

p {
  font-size: 2rem; }

a {
  text-decoration: none; }

h1,
h2,
h3 {
  margin: 0; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem; }

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

.centrar-texto {
  text-align: center; }

/**HEADER**/
.header {
  background-color: #4cb8b3; }
  .header h1 {
    color: #fff;
    padding: 2.5rem 0;
    text-align: center; }
  @media (min-width: 768px) {
    .header .contenido-header {
      display: flex;
      justify-content: space-between;
      align-items: center; } }
  .header.fijo {
    position: fixed;
    width: 100%;
    box-shadow: 0 0.3rem 0.4rem; }

.navegacion {
  text-align: center;
  padding-bottom: 2rem; }
  @media (min-width: 768px) {
    .navegacion {
      padding-bottom: 0; } }
  .navegacion a {
    padding-right: 2rem;
    font-size: 2rem;
    color: #fff; }
    .navegacion a:hover {
      color: #fef46d; }
    .navegacion a:last-of-type {
      padding-right: 0; }

/**VIDEO**/
.video {
  max-height: 70rem;
  overflow: hidden;
  position: relative; }
  .video .overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+67,ffffff+100&0.8+0,0.5+100 */
    background: -moz-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.6) 67%, rgba(255, 255, 255, 0.5) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#cc891c1e', endColorstr='#80ffffff',GradientType=1 );
    /* IE6-9 fallback on horizontal gradient */ }
  .video video {
    display: block;
    width: 100%; }

.contenido-video {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 100%;
  z-index: 1;
  position: relative; }
  .contenido-video h2 {
    color: #fff;
    font-size: 2.7rem; }
    @media (min-width: 768px) {
      .contenido-video h2 {
        font-size: 7rem; } }
  .contenido-video p {
    color: #fff;
    background-color: #f53756;
    padding: 0.7rem 1rem;
    margin: 0;
    font-weight: 700;
    font-size: 1rem; }
    @media (min-width: 768px) {
      .contenido-video p {
        font-size: 2rem;
        padding: 1rem 2rem; } }

/**INFORMACION**/
.informacion {
  margin: 5rem auto; }
  @media (min-width: 768px) {
    .informacion .contenido-informacion {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 5rem; } }

@media (min-width: 768px) {
  .texto-entrada {
    display: flex;
    flex-direction: column;
    justify-content: center; } }

.texto-entrada h2 {
  font-size: 3rem;
  margin: 1rem 0; }
  @media (min-width: 768px) {
    .texto-entrada h2 {
      font-size: 4rem; } }

.texto-entrada span {
  color: #f53756;
  font-weight: 700; }
  @media (min-width: 768px) {
    .texto-entrada span {
      font-size: 2rem; } }

.texto-entrada p {
  text-align: justify;
  line-height: 3rem; }

/**LINEUP**/
.lineup {
  background-color: #f53756; }
  .lineup h2 {
    text-align: center;
    color: #fff;
    padding: 5rem 0; }
  .lineup .fecha {
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    font-size: 2.5rem; }
  .lineup .lineup-contenedor {
    padding: 5rem 0; }
    @media (min-width: 768px) {
      .lineup .lineup-contenedor {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 5rem; } }

.nombre-escenario {
  text-align: center;
  padding: 2rem 0; }
  .nombre-escenario::after {
    content: "";
    display: block;
    width: 10rem;
    height: 10rem;
    background-size: 15rem;
    background-repeat: no-repeat;
    background-position: center center;
    margin: 2.5rem auto 0 auto; }

.lista {
  padding: 0;
  list-style: none; }
  .lista li {
    padding: 2.5rem 2rem;
    font-weight: 700;
    font-size: 1.8rem; }
    .lista li span {
      font-weight: 400; }

.bg-amarillo .lista li:nth-child(even) {
  background-color: #fef03a; }

.bg-verde .lista li:nth-child(even) {
  background-color: #3b9691; }

.rock .nombre-escenario::after {
  background-image: url(/build/img/icono_rock.png); }

.edm .nombre-escenario::after {
  background-image: url(/build/img/icono_edm.png); }

.bg-amarillo {
  background-color: #fef46d; }

.bg-verde {
  background-color: #4cb8b3; }

/**GALERÍA**/
.galeria {
  margin: 5rem auto; }
  .galeria h2 {
    text-align: center;
    margin-bottom: 5rem; }
  .galeria .galeria-imagenes {
    padding: 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem; }
    .galeria .galeria-imagenes li {
      list-style: none;
      padding: 0; }
    @media (min-width: 768px) {
      .galeria .galeria-imagenes {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 2rem; } }
    @media (min-width: 1200px) {
      .galeria .galeria-imagenes {
        display: grid;
        grid-template-columns: repeat(6, 1fr);
        gap: 2rem; } }

.overlay {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5); }

.btn-cerrar {
  background-color: #fff;
  padding: 1.5rem 2rem;
  border-radius: 50%;
  font-size: 2.5rem;
  font-weight: 900; }
  .btn-cerrar:hover {
    cursor: pointer; }

/*BOLETOS*/
.boletos {
  background-color: #4cb8b3;
  padding-bottom: 5rem; }
  .boletos h2 {
    text-align: center;
    color: #fff;
    padding: 5rem 0; }
  @media (min-width: 768px) {
    .boletos .contenido-boletos {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 5rem; } }

.tabla table {
  width: 100%;
  border-spacing: 0; }

.tabla p {
  text-align: center;
  color: #fff;
  font-size: 3rem; }

.tabla thead {
  background-color: #f53756; }
  .tabla thead th {
    font-size: 2rem;
    font-weight: 300;
    color: #fff;
    padding: 1rem 0; }

.tabla tbody {
  background-color: #fff; }
  .tabla tbody td {
    padding: 1rem;
    border-bottom: 1px solid #e1e1e1; }
  .tabla tbody tr:last-of-type td {
    border: none; }

.tabla tfoot {
  background-color: #752f97; }
  .tabla tfoot td {
    padding: 1.3rem 0;
    font-size: 3rem;
    text-align: center;
    color: #fff; }

/*FOOTER*/
.footer {
  background-color: #f53756; }
  .footer .contenedor-footer p {
    margin: 0;
    text-align: center;
    color: #fff;
    padding: 2rem;
    font-weight: 700; }
